import axios from "axios"

const state = {
    customers: [],
    pagination: {
        total: 0,
        per_page: 2,
        from: 1,
        to: 0,
        current_page: 1,
    },
}
const getters = {
    customers: state => state.customers,
    pagination: state=>state.pagination,
}

const mutations = {
    setCustomers(state, data) {
        state.customers = data.data.data
        state.pagination = data.data;
    }
}

const actions = {
    async getCustomers({ commit },search) {        
        await axios.post('customers/search',search).then(response => {
            commit('setCustomers', response)
        })
    }
}

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}
