
import Vue from 'vue'
import App from './layouts/theme2024/App.vue'
// import App from './layouts/2024/macos/App.vue'
import axios from 'axios'

import $ from 'jquery'
import BootstrapVue from 'bootstrap-vue'
import money from 'v-money'
import plugins from "./plugins/plugins"
import filters from "./plugins/filters"
import VueConfirmDialog from 'vue-confirm-dialog'
import VueToast from 'vue-toast-notification';
import 'vue-toast-notification/dist/theme-bootstrap.css';
import store from '@/store'

// import router from './configs/router'
import router from "./router/admin"

import 'bootstrap/dist/css/bootstrap.min.css';
// import "./assets/2024/macOS/style.css"
import "./assets/theme2024/style.css"
// import "./assets/theme.css"
import 'bootstrap/dist/js/bootstrap.bundle.js'

import VueI18n from 'vue-i18n' //needed for calendar locale

import {messages} from 'vue-bootstrap-calendar';

window.$ = $; // Đảm bảo jQuery có thể được sử dụng toàn cầu
window.jQuery = $;
Vue.config.productionTip = false
Vue.use(BootstrapVue)
Vue.use(VueConfirmDialog)
Vue.use(require('vue-moment'));
Vue.use(money, { precision: 4 })
Vue.use(plugins, filters)
Vue.use(VueToast, {
  position: 'top-right',
  pauseOnHover: true
});

Vue.use(require('vue-moment'));
Vue.use(VueI18n);

window.i18n = new VueI18n({
  locale: 'en',
  messages
});



window.API_URL = process.env.VUE_APP_API_URL;
window.axios = axios
axios.defaults.withCredentials = true
axios.defaults.baseURL = 'http://localhost:8080/econtractv2/public/api';
// axios.defaults.baseURL = 'http://donbadao.free.nf/econtractv2/public/api';
window.axios.defaults.headers.common['X-Requested-With'] = 'XMLHttpRequest';

axios.interceptors.request.use(
  config => {
    const token = localStorage.getItem('access_token');
    if (token) {
      config.headers['Authorization'] = `Bearer ${token}`;
    }
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);

axios.interceptors.response.use(
  (response) => {
    return response;
  },

  (error) => {
    if (error.response.status == 401) {
      store.dispatch('login/logOut')
      router.push({ name: 'login-index' })
      Vue.prototype.$econtract.hideLoading();
      return Promise.reject(error);
    }
    return Promise.reject(error);
  }
);

const DEFAULT_TITLE = 'Quản lý khách hàng';
router.afterEach((to, from, next) => {
  let title = to.meta.title != null ? to.meta.title : 'Bảng điều khiển'
  document.title = title + ' | ' + DEFAULT_TITLE;

  let isLogin = JSON.parse(localStorage.getItem('isLogin'))

  // if(!isLogin)
  //   router.to(401)

  if (to.fullPath != '/login')
    localStorage.setItem("currentPath", to.path)
});

// $("body").addClass('dark')

new Vue({
  el: '#app',
  router,
  store,
  i18n,
  render: h => h(App),
})
