import Vue from 'vue';
// Định nghĩa bộ lọc
Vue.filter('capitalize', function (value) {
    return value.toUpperCase();
});

Vue.filter('formatamout', function (amountIn=0, numberGits = 0) {
    let number = parseFloat(amountIn);
    
    if(number > 0){
        let format = number.toLocaleString("en-US", {
            minimumFractionDigits: numberGits,
            currency: "VND",
            style: "currency",
            useGrouping: true,
        });
    
        let split = format.split(".");
        let thousand = split[0];
        let decimal = split[1];
        let result = null;
        if (decimal > 0)
            result =
                thousand.replace("₫", "").replace(",", ".").replace(",", ".") + "," + decimal;
        else result = thousand.replace("₫", "").replace(",", ".").replace(",", ".");
    
        return result;
    }
    return 0;
  
   


    
});


export default {}

