import axios from "axios";

const state = {
    loginData: JSON.parse(window.localStorage.getItem('user_login')) || "",
    isLogin: JSON.parse(window.localStorage.getItem('isLogin')) || false,
    currentPath: window.localStorage.getItem('currentPath') || "",
}

const getters = {
    loginData: state => state.loginData,
    isLogin: state => state.isLogin,
    currentPath: state => state.currentPath,
}

const mutations = {
    login(state, payload) {
        axios
            .post(`/login`, {
                user_name: payload.username,
                password: payload.password,
            })
            .then((response) => {
                if (response.status == 200 && response.data.status_code == 200) {
                    let token = response.data.access_token.split("|")[1];
                    // state.loginData = response.data.user;
                    localStorage.setItem("access_token", token);
                    localStorage.setItem("user_login", JSON.stringify(response.data.user));
                    localStorage.setItem('isLogin', true)
                    state.isLogin = true;
                }
            });

    },
    logout(state) {
        let token = localStorage.getItem("access_token");
        console.log(token);
        axios.post(`login/logOut`, {
            headers: {
                'Authorization': `Bearer ${token}`  // Token người dùng hiện tại
            }
        }
        ).then(response => {
            console.log(response.data.status_code);
            // if (response.status == 200) {
            //     localStorage.setItem("access_token", null);
            //     localStorage.setItem("user_login", null);
            //     localStorage.setItem('isLogin', false)
            //     state.isLogin = false
            // }else if(response.status == 401){
            //     localStorage.setItem("access_token", null);
            //     localStorage.setItem("user_login", null);
            //     localStorage.setItem('isLogin', false)
            // }
        });
    }
}

const actions = {
    setLogin({ commit, state }, payload) {
        commit('login', payload);
    },
    logOut({ commit, state }) {
        localStorage.setItem("access_token", null);
        localStorage.setItem("user_login", null);
        localStorage.setItem('isLogin', false)
        state.isLogin = false
        //commit('logout', state);
    }
}


export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}