import Vue from 'vue';
import Vuex from 'vuex';


import reportsModule from "./modules/reports.js"
import registrationModule from './modules/registration.js';
import contractModules from "./modules/contracts.js"
import loginModules from "./modules/login.js"

import detailModules from "./modules/detail.js"
import productModules from "./modules/products.js"
import categoriesModules from "./modules/categories.js"
import templatesModules from "./modules/templates.js"

import revenuesModules from "./modules/revenues.js"

import companiesModules from './modules/companies.js';
import customersModules from './modules/customers.js';

Vue.use(Vuex);

export default new Vuex.Store({
    modules:{
        reports: reportsModule,
        registrations:registrationModule,
        contracts: contractModules,
        login: loginModules,
        detail: detailModules,
        products: productModules,
        categories: categoriesModules,
        templates: templatesModules,
        revenues: revenuesModules,
        companies: companiesModules,
        customers: customersModules
    }
})