<template>
  <nav class="navbar top-nav navbar-expand-lg w-100 sticky-top bg-blur border-bottom mb-2">
    <div class="container">
      <div id="navbarSupportedContent" class="collapse navbar-collapse">
      
        <ul class="navbar-nav ms-auto mt-2 mt-lg-0">
        
          <li class="nav-item dropdown">
            <a
              class="nav-link dropdown-toggle fw-bold text-dark"
              href="#"
              role="button"
              data-bs-toggle="dropdown"
              aria-expanded="false"
            >
              {{ loginData.user_fullname }}
            </a>
            <ul class="dropdown-menu">
              <li><a class="dropdown-item" href="#">Action</a></li>
              <li><a class="dropdown-item" href="#">Another action</a></li>
              <li><hr class="dropdown-divider" /></li>
              <li>
                <div class="hand dropdown-item fw-bold text-danger" @click="logOut">
                  <i class="fa-light fa-right-from-bracket"></i> Đăng xuất
                </div>
              </li>
            </ul>
          </li>
        </ul>
      </div>
    </div>
  </nav>
</template>

<script>
export default {
  props: {
    loginData: Object,
  },
  methods: {
    logOut() {
      if (!confirm("Bạn có muốn đăng xuất khỏi tài khoản không?")) return false;
      else {
        this.$store.dispatch("login/logOut");
        this.$router.push({
          name: "login-index",
        });
      }
    },
  },
};
</script>
