const state = {
    templates: []
}

const getters = {
    templates: state => state.templates
}

const mutations = {
    setTemplates(state, data) {
        state.templates = data
    }
}

const actions = {
    async getTemplates({ commit, state }) {
        let localTemplates = localStorage.getItem('nhl_templates');

        let templates = [];
        if (localTemplates == null)
            await axios.get(`/templates`).then((response) => {
                templates = response.data
                localStorage.setItem('nhl_templates', JSON.stringify(templates))
            });
        else
            templates = JSON.parse(localTemplates)

        commit('setTemplates', templates);
    }

}

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}