const state = {
    Detail: {
        cate_id: 0,
        product_id: 0,
        detail_product_price: 0,
        detail_product_component: 0,
        detail_content: "",
        detail_unit: "Cái",
        detail_quantity: 1,
        is_device: false,
        device_id: 0,
        extra1: "",
        extra2: "",
        detail_note: "",
    },
};
const getters = {
    Detail: state => state.Detail,

};
const mutations = {
    setDetail(state, object) {
        state.Detail = object;
    },
    setDetailNull(state) {
        state.Detail = {
            cate_id: 0,
            product_id: 0,
            detail_product_price: 0,
            detail_product_component: 0,
            detail_content: "",
            detail_unit: "Cái",
            detail_quantity: 1,
            is_device: false,
            device_id: 0,
            extra1: "",
            extra2: "",
            detail_note: ""
        }
    },
    updateData(state, data) {
        state.Detail.detail_product_price = data.detail_product_price;
        state.Detail.product_id = data.product_id;
    }   


};
const actions = {
    getDetail({ commit }, object) {
        commit('setDetail', object);
    },
    setDetailNull({ commit, state }) {
        commit('setDetailNull')
    },
    updateData({ commit, state }, data) {
        commit('updateData', data);
    }
};

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}