import axios from "axios"

const state = {
    companies: [],
    pagination: {
        total: 0,
        per_page: 2,
        from: 1,
        to: 0,
        current_page: 1,
    },
}
const getters = {
    companies: state => state.companies,
    pagination: state => state.pagination,
}



const mutations = {
    getCompaniesList(state, response) {
        state.companies = response.data.data;
        state.pagination = response.data;
    }
}

const actions = {
    async getCompanies({ commit }, array) {
        await axios
            .post(`/companies/search`, array)
            .then((response) => {
                commit('getCompaniesList', response);
            });
    }
}


export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}