
const state = {
    registration: {}
}

const getters ={
    registration: state =>state.registration
}

const mutations = {
    setRegistration(state,registration){
        state.registration = registration
    }
}

const actions = {
    setRegistration({commit,state},registration){
        commit('setRegistration',registration)
    }
}

export default {
    namespaced:true,
    state,
    getters,
    mutations,
    actions
}