import axios from "axios";

const state = {
    products: [],
}

const getters = {
    products: state => state.products,
};

const mutations = {
    setProducts(state, data) {
        state.products = data;
    },
}

const actions = {
    async getProducts({ commit }) {
        let localProducts = localStorage.getItem('nhl_products');
        let products = [];
        if (localProducts == null) {
            await axios.get('/products').then(response => {
                products = response.data;
                localStorage.setItem('nhl_products', JSON.stringify(products));
            })
        }
        else
            products = JSON.parse(localProducts)
        commit('setProducts', products);
    },

}

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}