const getters = {
    contractState: state => ({ ...state }),
    contracts: state=>(state.contracts),
    pagination: state=>state.pagination,
}

const mutations = {
    changeName(state, value) {
        state.testContract = value;
    },
    setRatio(state, ratios) {
        state.ratios = ratios;
    },
    async setContract(state, id) {
        state.contract_id = id;
        await axios.get(`/contracts/edit/${id}`).then(response => {
            state.Contract = response.data
        });
    },
    setDetail(state, data) {
        state.Contract.details.push(data);
    },
    setContracts(state,response){
        state.contracts = response.data.data;
        state.pagination=response.data;
    },
    setUpdateDetail(state, data) {
        console.log('----detail list---');
        console.log(state.Contract.details);
        console.log('index', data)
        // Object.assign(state.Contract.details[data.index], data.detail);
    },

}

const actions = {
    async getRatio({ commit, state }) {
        await axios.get(`/ratio`).then((response) => {
            commit('setRatio', response.data);
        });
    },

    getContract({ commit, state }, id) {
        commit('setContract', id);
    },
    addDetail({ commit, state }, data) {
        commit('setDetail', data)
    },
    updateDetail({ commit, state }, data) {
        commit('setUpdateDetail', data);
    },

    async getContracts({ commit, state }, search) {
        await axios
            .post(`contracts`, search)
            .then((response) => {
                commit('setContracts', response);
            })
            .catch((err) => { });
    }

}

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}