// import axios from "axios"

import axios from "axios"

const state = {
    reports: [],
    reportName: 'Nguyễn Hữu Lễ'
}
const getters = {
    reportName: state => state.reportName,
    reports: state => state.reports
}



const mutations = {
    setName(state, name) {
        state.reportName = name;
    },
    setReport(state, data) {
        state.reports = data
    }
}

const actions = {
    changeName({ commit }, name) {
        commit('setName', name);
    },
    async getReports({ commit }, search) {

        if (search != undefined) {
            await axios.post('reports/search', search).then(response => {
                console.log(response.data);
                commit('setReport', response.data)
            })
        }
    },
}


export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}