import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)


export default new VueRouter({
    // mode: 'history',
    routes:
        [
            {
                path: '/',
                name: 'dashboard-index',
                component: () => import("@/views/dashboard/Index.vue"),
                meta: {
                    title: 'Bảng điều khiển'
                },
            }
            , {
                path: '/login',
                name: 'login-index',
                component: () => import("@/views/login/Index.vue"),
                meta: {
                    title: 'Đăng nhập hệ thống'
                },
            },
            {
                path: '/dashboard',
                name: 'dashboard',
                component: () => import("../views/dashboard/Index.vue"),
                meta: {
                    title: 'Bảng điều khiển'
                },
            },
            {
                path: '/companies',
                name: 'companies',
                component: () => import("../views/companies/Index.vue"),
                meta: {
                    title: 'Danh sách công ty'
                },
            },
            {
                path: '/companies/create',
                name: 'company-create',
                component: () => import("../views/companies/Create.vue"),
                meta: {
                    title: 'Thêm mới công ty'
                },
            },
            {
                path: '/companies/edit/:id',
                name: 'company-edit',
                component: () => import("../views/companies/Create.vue"),
                meta: {
                    title: 'Cập nhật công ty'
                },
            },
            {
                path: '/contracts',
                name: 'contract-index',
                component: () => import("../views/contracts/Index.vue"),
                meta: {
                    title: 'Danh sách hợp đồng'
                },

            },
            {
                path: '/contracts/payment',
                name: 'contract-payment',
                component: () => import("../views/contractpayment/Index.vue"),
                meta: {
                    title: 'Danh sách hợp đồng đã thanh toán'
                },

            },
            {
                path: '/contracts/create',
                name: 'contract-create',
                component: () => import("../views/contracts/Create.vue"),
                meta: {
                    title: 'Tạo mới hợp đồng'
                },

            },
            {
                path: '/contracts/create2',
                name: 'contract-create2',
                component: () => import("../views/contracts/Create2.vue"),
                meta: {
                    title: 'Tạo mới hợp đồng'
                },

            },
            {
                path: '/contracts/edit/:id',
                name: 'contract-edit',
                component: () => import("../views/contracts/Create.vue"),
                meta: {
                    title: 'Cập nhật hợp đồng'
                },

            },
            {
                path: '/contracts/edit2/:id',
                name: 'contract-edit2',
                component: () => import("../views/contracts/Create2.vue"),
                meta: {
                    title: 'Cập nhật hợp đồng'
                },

            }, {
                path: '/contracts/detail/:id',
                name: 'contract-detail',
                component: () => import("../views/contracts/Detail.vue"),
                meta: {
                    title: 'Chi tiết hợp đồng'
                },
            },
            {
                path:'/contracts/option/:id',
                name: 'contract-option',
                component: ()=>import('../views/contracts/Option.vue'),
                meta:{
                    title:'Cập nhật thông tin hợp đồng'
                }
            },
            {
                path: '/commissions',
                name: 'commission-index',
                component: () => import("../views/commissions/Index.vue"),
                meta: {
                    title: 'Danh sách thanh toán hoa hồng'
                },
            },
            {
                path: '/contracts/expired/',
                name: 'contract-expired',
                component: () => import("../views/contract_expired/Index.vue"),
                meta: {
                    title: 'Hợp đồng hết hạn'
                },
            }, {
                path: '/commissions/create',
                name: 'commission-create',
                component: () => import("../views/commissions/Create.vue"),
                meta: {
                    title: 'Lập phiếu thanh toán hoa hồng'
                },
            }, {
                path: '/commissions/edit/:id',
                name: 'commission-edit',
                component: () => import("../views/commissions/Create.vue"),
                meta: {
                    title: 'Cập nhật phiếu thanh toán hoa hồng'
                },
            },
            {
                path: '/revenues',
                name: 'revenue-index',
                component: () => import("../views/revenues/Index.vue"),
                meta: {
                    title: 'Quản lý doanh thu'
                },
            },
            {
                path: '/templates',
                name: 'template-index',
                component: () => import("../views/templates/Index.vue"),
                meta: {
                    title: 'Danh sách mẫu hợp đồng'
                },
            },
            {
                path: '/products',
                name: 'product-index',
                component: () => import("../views/products/Index.vue"),
                meta: {
                    title: 'Danh sách dịch vụ'
                },
            },
            {
                path: '/debt',
                name: 'debt-index',
                component: () => import("../views/debt/Index.vue"),
                meta: {
                    title: 'Danh sách hợp đồng còn nợ'
                },
            },
            {
                path: '/reports',
                name: 'report-index',
                component: () => import("../views/reports/Index.vue"),
                meta: {
                    title: 'Báo cáo'
                }
            },
            {
                path: '/deliverys',
                name: 'delivery-index',
                component: () => import("../views/delivery/Index.vue"),
                meta: {
                    title: 'Biên bản bàn giao'
                }
            },
            {
                path: '/deliverys/create',
                name: 'delivery-add',
                component: () => import("../views/delivery/Add.vue"),
                meta: {
                    title: 'Tạo biên bản bàn giao'
                }
            },
            {
                path: '/deliverys/edit/:id',
                name: 'delivery-edit',
                component: () => import("../views/delivery/Add.vue"),
                meta: {
                    title: 'Tạo biên bản bàn giao'
                }
            },
            {
                path: '/customers/',
                name: 'customer-index',
                component: () => import("../views/customers/Index.vue"),
                meta: {
                    title: 'Danh sách khách hàng'
                }
            },
            {
                path: '/plans/',
                name: 'plan-index',
                component: () => import("../views/plans/Index.vue"),
                meta: {
                    title: 'Kế hoạch'
                }
            }
        ]
});

