import Vue from 'vue';
import Loading from "../extends/Loading"
const LoadingComponent = Vue.extend(Loading)

const Econtract = {
    install(Vue, options) {
        let loadingInstance
        Vue.component('Loading', Loading)
        Vue.prototype.$econtract = {


            showLoading() {
                if (!loadingInstance) {
                    loadingInstance = new LoadingComponent()
                    loadingInstance.$mount()
                    document.body.appendChild(loadingInstance.$el)
                }
            },
            hideLoading() {
                if (loadingInstance) {
                    document.body.removeChild(loadingInstance.$el)
                    loadingInstance.$destroy()
                    loadingInstance = null
                }
            },
            formatDate(date) {
                let d = new Date(date),
                    month = '' + (d.getMonth() + 1),
                    day = '' + d.getDate(),
                    year = d.getFullYear();

                if (month.length < 2)
                    month = '0' + month;
                if (day.length < 2)
                    day = '0' + day;

                return [day, month, year].join('/');
            },
            dateToMYSQL(date) {
                let inputDate = new Date(date);
                return inputDate.toISOString().slice(0, 19).replace('T', ' ');
            },

            paymentStatus(status) {
                switch (status) {
                    case 0:
                        return "Chưa thanh toán";
                    case 1:
                        return "Đã nhận tiền nhưng chưa nộp tiền"
                    case 2:
                        return "Đã xuất hóa đơn nhưng chưa thu tiền khách hàng"
                    case 3:
                        return "Đã nộp tiền"
                    case 4:
                        return "Đã nộp tiền và làm thanh toán"
                    default:
                        break;
                }

            },
            contractStatus(status) {
                switch (status) {
                    case 0:
                        return "Mới tạo lập";
                    case 1:
                        return "Đã trình ký"
                    case 2:
                        return "Đã gửi khách hàng"
                    case 3:
                        return "Hoàn thiện"
                    case 4:
                        return "Đã làm thanh toán"
                    default:
                        break;
                }
            },
            numberToVND(amount) {

                
                const units = ['', 'ngàn', 'triệu', 'tỷ'];
                const numberToWords = ['không', 'một', 'hai', 'ba', 'bốn', 'năm', 'sáu', 'bảy', 'tám', 'chín'];
                let word = '';
                let i = 0;

                while (amount > 0) {
                    let num = amount % 1000;
                    if (num > 0) {
                        let hundreds = Math.floor(num / 100);
                        let tens = Math.floor((num % 100) / 10);
                        let ones = num % 10;

                        if (hundreds > 0) {
                            word = numberToWords[hundreds] + ' trăm ' + word;
                        }
                        if (tens > 0) {
                            if (tens === 1) {
                                word = 'mười ' + word;
                            } else {
                                word = numberToWords[tens] + ' mươi ' + word;
                            }
                        }
                        if (ones > 0) {
                            if (tens === 0 && hundreds === 0) {
                                word = numberToWords[ones] + ' ' + word;
                            } else {
                                word = numberToWords[ones] + ' mốt ' + word;
                            }
                        }
                        word = word + ' ' + units[i] + ' ';
                    }
                    amount = Math.floor(amount / 1000);
                    i++;
                }

                return word + 'đồng';
            },
            formatAmout(amountIn, numberGits = 0) {
                let number = parseFloat(amountIn);
                let format = number.toLocaleString("en-US", {
                    minimumFractionDigits: numberGits,
                    currency: "VND",
                    style: "currency",
                    useGrouping: true,
                });
                let split = format.split(".");
                let thousand = split[0];
                let decimal = split[1];
                let result = null;
                if (decimal > 0)
                    result =
                        thousand.replace("₫", "").replace(",", ".").replace(",", ".") + "," + decimal;
                else result = thousand.replace("₫", "").replace(",", ".").replace(",", ".");

                return result;
                // let amount = parseFloat(amountIn)
                // const formattedAmount = amount.toLocaleString("en-US", {
                //     style: "currency",
                //     currency: "VND",
                //     useGrouping: true,
                //     minimumFractionDigits: numberGits,
                // });
                // return formattedAmount.replace("₫", "").replace(/,/g, ".");
            },
            formatCurrency(el, binding, vnode) {
                // Lấy giá trị của directive
                const value = binding.value;

                // Định dạng giá trị
                const formattedValue = value.toFixed(6).replace(/\./g, ",").replace(/(\d)(?=(\d{3})+\.)/g, "$1,");

                // Thay đổi nội dung của phần tử
                el.textContent = formattedValue;
            },
            // let amount = parseFloat(amountIn)
            // const formattedAmount = amount.toLocaleString("en-US", {
            //     style: "currency",
            //     currency: "VND",
            //     useGrouping: true,
            //     minimumFractionDigits: numberGits,
            // });
            // return formattedAmount.replace("₫", "").replace(/,/g, ".");
            // },


            doc1so(so) {
                var arr_chuhangdonvi = ['không', 'một', 'hai', 'ba', 'bốn', 'năm', 'sáu', 'bảy', 'tám', 'chín'];
                var resualt = '';
                resualt = arr_chuhangdonvi[so];
                return resualt;
            },
            doc2so(so) {
                so = so.replace(' ', '');
                var arr_chubinhthuong = ['không', 'một', 'hai', 'ba', 'bốn', 'năm', 'sáu', 'bảy', 'tám', 'chín'];
                var arr_chuhangdonvi = ['mươi', 'mốt', 'hai', 'ba', 'bốn', 'lăm', 'sáu', 'bảy', 'tám', 'chín'];
                var arr_chuhangchuc = ['', 'mười', 'hai mươi', 'ba mươi', 'bốn mươi', 'năm mươi', 'sáu mươi', 'bảy mươi', 'tám mươi', 'chín mươi'];
                var resualt = '';
                var sohangchuc = so.substr(0, 1);
                var sohangdonvi = so.substr(1, 1);
                resualt += arr_chuhangchuc[sohangchuc];
                if (sohangchuc == 1 && sohangdonvi == 1)
                    resualt += ' ' + arr_chubinhthuong[sohangdonvi];
                else if (sohangchuc == 1 && sohangdonvi > 1)
                    resualt += ' ' + arr_chuhangdonvi[sohangdonvi];
                else if (sohangchuc > 1 && sohangdonvi > 0)
                    resualt += ' ' + arr_chuhangdonvi[sohangdonvi];

                return resualt;
            },
            doc3so(so) {
                var resualt = '';
                var arr_chubinhthuong = ['không', 'một', 'hai', 'ba', 'bốn', 'năm', 'sáu', 'bảy', 'tám', 'chín'];
                var sohangtram = so.substr(0, 1);
                var sohangchuc = so.substr(1, 1);
                var sohangdonvi = so.substr(2, 1);
                resualt = arr_chubinhthuong[sohangtram] + ' trăm';
                if (sohangchuc == 0 && sohangdonvi != 0)
                    resualt += ' linh ' + arr_chubinhthuong[sohangdonvi];
                else if (sohangchuc != 0)
                    resualt += ' ' + this.doc2so(sohangchuc + ' ' + sohangdonvi);
                return resualt;
            },

            docsonguyen(so) {

                var result = '';
                if (so != undefined) {
                    //alert(so);
                    var arr_So = [{ ty: '' }, { trieu: '' }, { nghin: '' }, { tram: '' }];
                    var sochuso = so.length;
                    for (var i = (sochuso - 1); i >= 0; i--) {

                        if ((sochuso - i) <= 3) {
                            if (arr_So['tram'] != undefined)
                                arr_So['tram'] = so.substr(i, 1) + arr_So['tram'];
                            else arr_So['tram'] = so.substr(i, 1);

                        }
                        else if ((sochuso - i) > 3 && (sochuso - i) <= 6) {
                            if (arr_So['nghin'] != undefined)
                                arr_So['nghin'] = so.substr(i, 1) + arr_So['nghin'];
                            else arr_So['nghin'] = so.substr(i, 1);
                        }
                        else if ((sochuso - i) > 6 && (sochuso - i) <= 9) {
                            if (arr_So['trieu'] != undefined)
                                arr_So['trieu'] = so.substr(i, 1) + arr_So['trieu'];
                            else arr_So['trieu'] = so.substr(i, 1);
                        }
                        else {
                            if (arr_So.ty != undefined)
                                arr_So.ty = so.substr(i, 1) + arr_So.ty;
                            else arr_So.ty = so.substr(i, 1);
                        }
                        //console.log(arr_So);
                    }

                    if (arr_So['ty'] > 0)
                        result += this.doc(arr_So['ty']) + ' tỷ';
                    if (arr_So['trieu'] > 0) {
                        if (arr_So['trieu'].length >= 3 || arr_So['ty'] > 0)
                            result += ' ' + this.doc3so(arr_So['trieu']) + ' triệu';
                        else if (arr_So['trieu'].length >= 2)
                            result += ' ' + this.doc2so(arr_So['trieu']) + ' triệu';
                        else result += ' ' + this.doc1so(arr_So['trieu']) + ' triệu';
                    }
                    if (arr_So['nghin'] > 0) {
                        if (arr_So['nghin'].length >= 3 || arr_So['trieu'] > 0)
                            result += ' ' + this.doc3so(arr_So['nghin']) + ' nghìn';
                        else if (arr_So['nghin'].length >= 2)
                            result += ' ' + this.doc2so(arr_So['nghin']) + ' nghìn';
                        else result += ' ' + this.doc1so(arr_So['nghin']) + ' nghìn';
                    }
                    if (arr_So['tram'] > 0) {
                        if (arr_So['tram'].length >= 3 || arr_So['nghin'] > 0)
                            result += ' ' + this.doc3so(arr_So['tram']);
                        else if (arr_So['tram'].length >= 2)
                            result += ' ' + this.doc2so(arr_So['tram']);
                        else result += ' ' + this.doc1so(arr_So['tram']);
                    }
                }
                result += ' đồng';
                return result;
            },

            doc(so) {
               
                var kytuthapphan = ",";
                var result = '';
                if (so != undefined) {
                    so = " " + so + " ";
                    so = so.trim();
                    var cautrucso = so.split(kytuthapphan);
                    if (cautrucso[0] != undefined) {
                        result += this.docsonguyen(cautrucso[0]);
                    }
                    if (cautrucso[1] != undefined) {
                        //alert(this.docsonguyen(cautrucso[1]));
                        result += ' phẩy ' + this.docsonguyen(cautrucso[1]);
                    }
                }

                let resultTrim = result.trim();
                return resultTrim.charAt(0).toUpperCase() + resultTrim.slice(1);
            }


        };
    }
};

Vue.use(Econtract);

export default Econtract;
