<template>
  <div id="sidebar" class="d-flex flex-column flex-shrink-0 bg-light position-fixed">
    <div class="form group position-relative align-middle p-2">
      <!-- <i class="fa-thin fa-magnifying-glass position-absolute"></i> -->
      <input
        type="text"
        class="form-control"
        v-model="keyword"
        placeholder="Tìm kiếm menu..."
      />
    </div>
    <div class="sidebar-content">
      <ul class="nav nav-pills nav-flush flex-column">
        <li class="nav-item" v-for="menu in search" :key="menu.key">
          <router-link :to="menu.link" :title="menu.title" class="nav-link">
            <i :class="menu.icon + ' fa-1x menu-icon'"></i>
            <span class="fw-normal ms-2 menu-name"> {{ menu.title }}</span>
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      keyword: "",

      menus: [
        {
          key: "dashboard",
          link: "/dashboard",
          icon: "fa-solid fa-gauge",
          title: "Trang chủ",
        },
        {
          key: "company",
          link: "/companies",
          icon: "fa-solid fa-building",
          title: "Danh sách công ty",
        },
        {
          key: "contract",
          link: "/contracts",
          icon: "fa-solid fa-file-contract",
          title: "Danh sách hợp đồng",
        },
        {
          key: "contract-expired",
          link: "/contracts/expired",
          icon: "fa-solid fa-file-contract",
          title: "Hợp đồng hết hạn",
        },
        {
          key: "revenues",
          link: "/revenues",
          icon: "fa-solid fa-chart-mixed",
          title: "Quản lý doanh thu",
        },
        {
          key: "commissions",
          link: "/commissions",
          icon: "fa-solid fa-sack-dollar",
          title: "Thanh toán hoa hồng",
        },
        {
          key: "templates",
          link: "/templates",
          icon: "fa-solid fa-cubes-stacked",
          title: "Danh sách mẫu hợp đồng",
        },
        {
          key: "products",
          link: "/products",
          icon: "fa-solid fa-cube",
          title: "Danh sách dịch vụ",
        },
        {
          key: "debt",
          link: "/debt",
          icon: "fa-solid fa-sack-dollar",
          title: "Hợp đồng còn nợ",
        },
        {
          key: "report",
          link: "/reports",
          icon: "fa-solid fa-chart-simple",
          title: "Báo cáo",
        },
        {
          key: "delivery",
          link: "/deliverys",
          icon: "fa-solid fa-file-lines",
          title: "Biên bản bàn giao",
        },
        {
          key: "contractpayment",
          link: "/contracts/payment",
          icon: "fa-solid fa-file-invoice-dollar",
          title: "Hợp đồng đã thanh toán",
        },
        {
          key: "customer",
          link: "/customers",
          icon: "fa-solid fa-users",
          title: "Danh sách khách hàng",
        },
      ],
    };
  },
  computed: {
    search() {
      return this.menus.filter((item) => {
        return item.title.toLowerCase().indexOf(this.keyword.toLowerCase()) !== -1;
      });
    },
  },
};
</script>
